import React from 'react'
import ImageSectionStyles from '../styles/ComponentStyles/ImageSectionStyles'
import resumeImg from "../images/Me.jpg";
import PrimaryButton from './PrimaryButton';

function ImageSection() {
  return (
    <ImageSectionStyles>
      <div className="left-content">
        <img src={resumeImg} alt="Mon profil" />
      </div>
      <div className="right-content">
        <h4>Je suis <span>Ivan NTEUMI</span></h4>
        <p className="paragraphy">
          Etudiant en Gnie Logiciel passionn par le dveloppement informatique et la cration de solutions innovantes. Mon parcours acadmique, combin  mes projets personnels, m'a permis d'acqurir des comptences solides en conception logicielle, dveloppement web et mobile, ainsi qu'en gestion de donnes.
          Ce portfolio est une vitrine de mon travail, o je partage mes projets, mes comptences et ma passion pour l'univers du dveloppement. N'hsitez pas  explorer mes ralisations et  me contacter pour toute question ou collaboration.
          Merci de votre visite et bonne dcouverte ! 
        </p>
        <div className="about-info">
          <div className="info-title">
            <p>Nom</p>
            <p>Age</p>
            <p>Nationalit</p>
            <p>Langue</p>
            <p>Localisation</p>
          </div>
          <div className="info">
            <p>: Ivan NTEUMI</p>
            <p>: 19</p>
            <p>: Camerounais</p>
            <p>: Franais, Anglais</p>
            <p>: Douala-Yassa, Cameroun</p>
          </div>
        </div>
        <PrimaryButton title={"Download CV"} />
      </div>
    </ImageSectionStyles>
  )
}

export default ImageSection